.work {
  position: relative;
  display: flex;
  width: 100%;
  margin-top: 15px;
  margin-bottom: 50px;
  background: rgba(0, 0, 0, 0.116);
  padding: 20px;
  border-radius: 50px;
  backdrop-filter: blur(20px);
}

.work .work-img {
  width: 400px;
  height: 300px;
  border-radius: 40px;
  border: 2px solid var(--color-sec-light);
  margin: auto;
  transition: .5s ease;
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
}

.work:hover > .work-img {
  background-position: center;
}

.work .work-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 50px;
  max-width: 50%;
}

.work .work-info .name {
  font-size: 40px;
  font-weight: 500;
}

.work .work-info .position {
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 2px;
}

.work .work-info .about {
  margin-top: 40px;
  font-weight: 100;
  font-size: 17px;
}

.work .work-info .contact-work {
  align-self: flex-start;
  background-color: var(--color-sec-light);
  padding: 17px 35px;
  font-size: 18px;
  border-radius: 50px;
  margin-top: 40px;
  transition: 0.1s ease;
}

.work .work-info .contact-work:hover {
  background-color: transparent;
  outline: 2px solid var(--color-sec-light);
}

@media (min-width: 1920px) {

  .work {
    width: 100% !important;
    margin-bottom: 35px !important;
    padding: 30px !important;
  }
  .work .work-img {
    width: 100% !important;
    height: 415px !important;
  }

  .work .work-info {
    max-width: 50% !important;
  }

  .work .work-info .name {
